@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap);
html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

:root {
    --nav-color: rgba(10, 90, 255, 1);
    --nav-font-size: calc(15px + 2vmin);
    --nav-box-shadow: 0px 0px 9px 3px rgba(41, 41, 41, 0.25);
    --navbar-height: 8vh;

    --ds-font: 'Dancing Script', 'cursive';
}

/* SELECTORS */
/* Direct */
nav {
    position: fixed;
    min-height: 8vh;
    min-height: var(--navbar-height);
    justify-content: space-around;
    width: 100%;
    background-color: rgba(10, 90, 255, 1);
    background-color: var(--nav-color);
    display: flex;
    flex-direction: row;
    align-items: center;
    box-shadow: 0px 0px 9px 3px rgba(41, 41, 41, 0.25);
    box-shadow: var(--nav-box-shadow);
    z-index: 1000;
}

nav ul {
    display: flex;
    list-style-type: none;
    justify-content: space-around;
    flex-direction: row;
    width: 30%;
}

nav ul li {
    font-family: 'Dancing Script', 'cursive';
    font-family: var(--ds-font);
    font-weight: bold;
    font-size: calc(15px + 2vmin);
    font-size: var(--nav-font-size);
}

nav ul li a {
    color: white;
    text-decoration: none;
}

.logo {
    margin-top: 9px;
    margin-left: 10px;
    width: calc(15px + 2vmin);
    width: var(--nav-font-size);
    height: auto;
}

.logo-text {
    font-weight: bold;
    color: white;
    font-family: 'Dancing Script', 'cursive';
    font-family: var(--ds-font);
    font-size: calc(15px + 2vmin);
    font-size: var(--nav-font-size);
}

.collapse {
    display: none;
    cursor: pointer;
}

.collapse div {
    width: 25px;
    height: 2px;
    background-color: white;
    margin: 5px;
}

@media screen and (max-width: 1180px) {
    nav ul {
        width: 50%;
    }
}

@media screen and (max-width: 768px) {
    body {
        overflow-x: hidden;
    }

    nav ul {
        position: absolute;
        right: 0px;
        height: calc( 100vh - 8vh);
        height: calc( 100vh - var(--navbar-height) );
        top: 8vh;
        background-color: rgba(10, 90, 255, 1);
        background-color: var(--nav-color);
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 50%;
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
        transition: -webkit-transform 0.5s ease-in;
        transition: transform 0.5s ease-in;
        transition: transform 0.5s ease-in, -webkit-transform 0.5s ease-in;
    }

    nav ul li {
        opacity: 0;
    }

    .collapse {
        display: block;
    }
}

.nav-active {
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
}

@-webkit-keyframes navLinkFade {
    from {
        opacity: 0;
        -webkit-transform: translateX(50px);
                transform: translateX(50px);
    }

    to {
        opacity: 1;
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
}

@keyframes navLinkFade {
    from {
        opacity: 0;
        -webkit-transform: translateX(50px);
                transform: translateX(50px);
    }

    to {
        opacity: 1;
        -webkit-transform: translateX(0px);
                transform: translateX(0px);
    }
}
